<template>
  <div v-if="selectComp" class="page-view">
    <div class="comp-tip">
      <div>
        <template v-if="authComp">
          <span class="comp-name">{{ selectComp.name }}</span>
          <template v-if="authComp.isInvoice">
            <vxe-button v-if="$utils.isWithin30Day(authComp.authDate)" type="text" class="invoice-btn" status="primary" @click="$refs.invoiceInfo.open()" content=""></vxe-button>
          </template>
          <template v-else-if="selectComp.invoice">
            <span v-if="$utils.isWithin7Day(authComp.authDate)" class="comp-invoice">
              <span class="msg-dot">1</span>
              <vxe-button type="text" class="invoice-btn" status="primary" @click="$refs.invoiceInfo.submit()">
                <span></span>
                <span class="red" style="font-size: 12px;">({{ $utils.toDateString($utils.getWhatDay(authComp.authDate, 7), 'yyyy-MM-dd') }})</span>
              </vxe-button>
            </span>
          </template>
        </template>
        <template v-else>
          <vxe-select class="comp-name" v-model="selectGroupComp.selected" :options="selectGroupComp.options" :option-props="{label: 'name', value: 'code'}" size="mini"></vxe-select>
          <span class="comp-type"> - {{ $utils.getCompType(selectComp.authCodeType) }}</span>
          <template v-if="selectComp.pricing !== selectComp.amount">
            <span class="comp-pricing">{{ selectComp.pricing }}</span>
            <span class="comp-amoun">{{ selectComp.amount }}</span>
          </template>
          <template v-else>
            <span class="comp-amoun">{{ selectComp.amount }}</span>
          </template>
          <span class="comp-expire">/ {{ $utils.getCompExpiration(selectComp.deadline) }}</span>
        </template>
      </div>
      <template v-if="loginInfo && authComp">
        <ul>
          <li><span class="green bold">{{ $utils.getCompType(authComp.authType) }}</span></li>
          <li><span class="green bold">{{ $utils.getCompExpiration(authComp.deadline) }}</span></li>
          <li><span class="green bold">{{ authComp.version }}</span></li>
        </ul>
      </template>
      <template v-else>
        <ul>
          <li>{{ selectComp.authCodeType === 'src' ? '' : ''}}</li>
        </ul>
      </template>
      <div class="comp-agreement">
        <a class="link" @click="$refs.authAgreement.open()"></a>
      </div>
      <div class="comp-oper">
        <vxe-button class="btn" status="danger" @click="$refs.compOrder.open()" v-if="!authComp"></vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseLog.open()">{{ authComp ? ' v3.0 ' : ' v3.0 ' }}</vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseNextLog.open()">{{ authComp ? ' v4.0 ' : ' v4.0 ' }}</vxe-button>
        <vxe-button class="btn" @click="$router.replace({name: backName})"></vxe-button>
      </div>
    </div>
    <div class="comp-preview">
      <div class="preview">
        <img :src="imgUrl">
      </div>
      <ul class="features">
        <li v-for="(item, index) in selectComp.features" :key="index">{{ item }}</li>
      </ul>
    </div>
    <div class="comp-api">
      <a class="link" href="/vxe-table/#/table/renderer/edit" target="_black"></a>
    </div>
    <div class="comp-desc">
      <pre>
        <pre-code class="text">{{ apiDesc[0] }}</pre-code>
      </pre>
    </div>
    <pre>
      <pre-code class="html">{{ demoCodes[0] }}</pre-code>
      <pre-code class="javascript">{{ demoCodes[1] }}</pre-code>
    </pre>
    <release-log ref="releaseLog" :code="selectComp.code"></release-log>
    <release-log ref="releaseNextLog" :code="selectComp.code" :release-code="`${selectComp.code}_Next`"></release-log>
    <comp-order ref="compOrder" :code="selectComp.code"></comp-order>
    <auth-agreement ref="authAgreement" :code="selectComp.code"></auth-agreement>
    <invoice-info ref="invoiceInfo" :code="selectComp.code"></invoice-info>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      backName: 'Filter',
      imgUrl: require('../../assets/image/plugin/EditDownTextarea.gif'),
      apiDesc: [
        `
         vxe-tablenode-sass 

         Demo 

        
         plugins/*  *.vue 

        3?*~
        ?
        *
        ~ ?* ~  ?* ~
        '~?'  '?'
        '~*'  '*'
        '~~'  '~'
        
        
        filters=[{ data: { checks: [], sVal: '', sMenu: '', fType1: '', fVal1: '', fMode: 'and', fType2: '', fVal2: '' } }]
        
        
        checks
        sVal: 
        sMenu: 
        fType1: 
        fVal1: 
        fMode: 
        fType2: 
        fVal2: 
        
        
        filter-render={ name: 'FilterCombination', cellType: 'string', props: {} }
        
        
        cellType: stringnumber
        props vxe-input 
        `
      ],
      demoCodes: [
        `
        <vxe-table
          border
          show-overflow
          height="500"
          :data="tableData">
          <vxe-table-column type="seq" width="60"></vxe-table-column>
          <vxe-table-column field="a" title="A1"></vxe-table-column>
          <vxe-table-column field="b" title="B1" :filters="[{ data: { checks: [], sVal: '' } }]" :filter-render="{ name: 'FilterAggregation' }"></vxe-table-column>
          <vxe-table-column field="c" title="C1" :filters="[{ data: { checks: [], sVal: '', sMenu: '', fType1: '', fVal1: '', fMode: 'and', fType2: '', fVal2: '' } }]" :filter-render="{ name: 'FilterCombination', cellType: 'number' }"></vxe-table-column>
          <vxe-table-column field="d" title="D1" :filters="[{ data: { checks: [], sVal: '', sMenu: '', fType1: '', fVal1: '', fMode: 'and', fType2: '', fVal2: '' } }]" :filter-render="{ name: 'FilterCombination' }"></vxe-table-column>
        </vxe-table>
        `,
        `
        export default {
          data () {
            return {
              tableData: [
                { id: 10001, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10002, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10003, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10004, a: '', b: '', c: '', d: '', e: '', f: '', g: '' }
              ]
            }
          }
        }
        `
      ]
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList',
      'appCompList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList && this.selectGroupComp) {
        return this.loginInfo.compList.find(comp => this.selectGroupComp.options.some(gItem => gItem.code === comp.appCode))
      }
      return null
    },
    selectGroupComp () {
      return this.appCompList.find(item => item.gCode === 'EditDownTextarea')
    },
    selectComp () {
      const gItem = this.selectGroupComp
      if (gItem) {
        return gItem.options.find(item => item.code === gItem.selected)
      }
      return null
    }
  }
}
</script>
